          animation: pulse 2.5s linear infinite; }

@-webkit-keyframes pulse {
  0% {
    -webkit-transform: scale(1, 1);
            transform: scale(1, 1);
    opacity: .25; }
  50% {
    -webkit-transform: scale(1.2, 1);
            transform: scale(1.2, 1);
    opacity: 1; }
  100% {
    -webkit-transform: scale(1, 1);
            transform: scale(1, 1);
    opacity: .25; } }

@keyframes pulse {
  0% {
    -webkit-transform: scale(1, 1);
            transform: scale(1, 1);
    opacity: .25; }
  50% {
    -webkit-transform: scale(1.2, 1);
            transform: scale(1.2, 1);
